@import 'vars';
@import 'fonts';
@import 'reset';
@import 'libs';
@import 'global';

@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@700&family=Roboto:wght@700&display=swap');


.header{
    background-image: url(../img/Group20.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 2300px;
    padding: 80px;
    padding-bottom: 0;
    position: relative;
    &__content{

        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__btn{
        display: flex;
        align-items: center;
    }
    &__item-menu ul,li{
        display: flex;
        align-items: center;
    }
    &__item-menu ul,a{
        color: #fff;
        margin: 0 17px;
        font-weight: 700;
        font-size: 18px;
        line-height: 22px;
    }
    &__account-text{
        margin-left: 10px;
    }
    .logoimg{
       max-width: 100%;
        height: auto;
    }
}

.footer{
    &__content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom:  120px;
        margin-top: 200px;
    }
    &__item-text{
        margin-top: 24px;
        margin-bottom: 122px;
        width: 300px;
    p{
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
    }
    }
    &__span{
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    opacity: 0.5;
    bottom: 0;
    }
    &__lists-text{
        margin-bottom: 24px;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        color: #FBD784;
    }
    &__lists {
        min-height: 280px;
    }
    &__lists ul,li{
        list-style-type: none;
        margin-left: 0;
        padding-left: 0;
        margin-top: 16px;
        font-weight: 500;
        font-size: 18px;
        line-height: 32px;
        transition: all 2.5s;
        :hover{
            color: #FBD784;
        }
    }
}



@media (max-width: 700px) {
    .header{
        &__item-menu ul,li{
            flex-wrap: wrap;
        }
    
    }
    .header__content {
        flex-wrap: wrap;
    }
    .footer__content{
        flex-wrap: wrap;
        padding-bottom: 0;
    }
    .header__item-menu ul, .header{

        padding: 10px;
    }
    .header a {
        padding: 10px;
    }
}

// ======================================================== Contener ==============================================================================

.page{
    &__inner{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__bifir{
        font-weight: 800;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 6px;
        text-transform: uppercase;
        color: #FBD784;
        position: relative;
        padding-left: 15%;
        &::after{
            position: absolute;
            content: '';
            top: 10px;
            left: 0;
            border-top: 3px solid;
            width: 72px;
            height: 2px;

        }
    }
    &__items{
        top: 0;
        position: absolute;
        margin-top: 285px;
    }
    &__text{
        padding-top: 54px;
        font-family: 'Merriweather', serif;
        font-style: normal;
        width: 1067px;
font-weight: 600;
font-size: 88px;
line-height: 144%;
text-transform: capitalize;
    }
    &__down{
        position: relative;
        top: 32px;
        font-style: normal;
font-weight: 700;
font-size: 18px;
line-height: 22px;

        &::after{
            content: '↓';
            position: absolute;
            top: 0;
            left: 115px;
            width: 16px;
height: 24px;
        }
    }
}

// ====================================================== section-content =======================================================================

.started{
    &__inner{
        
    }
    &__namber{
        margin-top: 38px;
        position: relative;
    }
    &__namber h2{
        top: 0;
        font-weight: 700;
        font-size: 240px;
        line-height: 240px;
        opacity: 0.2;
        position: absolute;
    }
    &__items{
        top: 0;
        position: absolute;
        margin-top: 1440px;
        display: flex;
        justify-content: space-between;
    }
    &__content{
        margin: 141px 114px 141px 150px;
    }
    &__content h2{
        font-family: 'Merriweather', serif;
        font-weight: 600;
        font-size: 64px;
        line-height: 77px;
        flex: none;
        width: 555px;
order: 1;
align-self: flex-start;
flex-grow: 0;
padding-top: 27px;
    }
    &__content p{
        font-weight: 700;
font-size: 18px;
line-height: 32px;
width: 632px;
flex: none;
order: 2;
align-self: center;
flex-grow: 0;
padding: 27px 0 27px  0;
    }
    &__content span{
        font-size: 18px;
        line-height: 22px;
        color: #FBD784;
        position: relative;
        &::before{
            position: absolute;
            content: '➜';
            padding-left: 125%;
        }
    }
    &__img{
        max-width: 100%;
        height: auto;
    }
}

.started__items02{
